import React from 'react';
import leao from './Leao.jpg';
import './styleCard1.css';

function Card5() {
    return (
        <div className='card5'>
            <img src={leao} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Aiolia</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>LEÃO</h3>
                    <p>23/07 - 22/08</p>
                    <br></br>
                    <p>As pessoas do signo de Leão são bons amigos, generosos, inteligentes, idealistas, decididos, afetuosos e estão sempre dispostos a ajudar.</p>
                </div>
            </div>
        </div>
    );
}

export default Card5;