import React from 'react';
import escorpiao from './Escorpiao.jpg';
import './styleCard1.css';

function Card8() {
    return (
        <div className='card8'>
            <img src={escorpiao} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Milo</h2>
               
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>ESCORPIÃO</h3>
                    <p>23/10 - 21/11</p>
                    <br></br>
                    <p>As pessoas do signo de Escorpião são questionadoras, otimistas, intensas e marcadas pelo o ar de mistério que as cercam. </p>
                </div>
            </div>
        </div>
    );
}

export default Card8;