import React from 'react';
import aquario from './Aquario.jpg';
import './styleCard1.css';

function Card11() {
    return (
        <div className='card11'>
            <img src={aquario} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Camus</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>AQUÁRIO</h3>
                    <p>20/01 - 18/02</p>
                    <br></br>
                    <p>As pessoas do signo de Aquário têm sempre um objetivo pelo qual desejam lutar. Idealistas, anseiam por um mundo melhor para todos e contribuem com essa mudança. São originais, independentes, inteligentes e possuem a intuição aguçada.</p>
                </div>
            </div>
        </div>
    );
}

export default Card11;