import React from 'react';
import sagitario from './Sagitario.jpg';
import './styleCard1.css';

function Card9() {
    return (
        <div className='card9'>
            <img src={sagitario} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Aiolos</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>SAGITÁRIO</h3>
                    <p>22/11 - 21/12</p>
                    <br></br>
                    <p>As pessoas de Sagitário são expansivas, alegres, animadas e positivas. Estimular quem deseja realizar um sonho é com esses nativos mesmo.São destaque em qualquer lugar.</p>
                </div>
            </div>
        </div>
    );
}

export default Card9;