import React from 'react';
import gemeos from './Gemeos.jpg';
import './styleCard1.css';

function Card3() {
    return (
        <div className='card3'>
            <img src={gemeos} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Saga</h2>
                
            </div>
            <div className='overlay'>
                <div className='text'>
                    <h3>GÊMEOS</h3>
                    <p>21/05 - 21/06</p>
                    <br></br>
                    <p>Gêmeos é um dos signos mais divertidos, alegres e animados. Com uma boa conversa, as pessoas desse signo são capazes de afastar qualquer monotonia.</p>
                </div>
            </div>
        </div>
    );
}

export default Card3;