import React from 'react';
import peixes from './Peixes.jpg';
import './styleCard1.css';

function Card12() {
    return (
        <div className='card12'>
            <img src={peixes} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Afrodite</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>PEIXES</h3>
                    <p>19/02 - 20/03</p>
                    <br></br>
                    <p>As pessoas do signo de Peixes são pessoas sensíveis, sonhadoras, empáticas, pacientes, calmas, intuitivas, românticas, generosas, místicas e amáveis.</p>
                </div>
            </div>
        </div>
    );
}

export default Card12;