import React from 'react';
import cancer from './Cancer.jpg';
import './styleCard1.css';

function Card4() {
    return (
        <div className='card4'>
            <img src={cancer} className="App-logo" alt="logo" />
            <div className='info'>
            
            <h2>Máscara da Morte</h2>
            
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>CÂNCER</h3>
                    <p>22/06 - 22/07</p>
                    <br></br>
                    <p>As pessoas do signo de Câncer estão entre as mais sensíveis do zodíaco. São românticas, sonhadoras, fiéis, cuidadosas e muito dedicadas à família.</p>
                    </div>
            </div>
        </div>
    );
}

export default Card4;