import React from 'react';
import libra from './Libra.jpg';
import './styleCard1.css';

function Card7() {
    return (
        <div className='card7'>
            <img src={libra} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Dohko</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>LIBRA</h3>
                    <p>23/09 - 22/10</p>
                    <br></br>
                    <p>As pessoas do signo de Libra são pessoas diplomáticas, justiceiras, pacíficas e sociáveis. Conversam com todos e têm uma boa relação social nos espaços em que frequentam. </p>
                </div>
            </div>
        </div>
    );
}

export default Card7;