import React from 'react';
import virgem from './Virgem.jpg';
import './styleCard1.css';

function Card6() {
    return (
        <div className='card6'>
            <img src={virgem} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Shaka</h2>
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>VIRGEM</h3>
                    <p>23/08 - 22/09</p>
                    <br></br>
                    <p>As pessoas do signo de Virgem costumam ser inteligentes, detalhistas, metódicas, organizadas, discretas e perfeccionistas.</p></div>
            </div>
        </div>
    );
}

export default Card6;