import React from 'react';
import touro from './Touro.jpg';
import './styleCard1.css';

function Card2() {
    return (
        <div className='card2'>
            <img src={touro} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Aldebaran</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>TOURO</h3>
                    <p>20/04 - 20/05</p>
                    <br></br>
                    <p>Touro é um signo que gosta de desafios e luta com todas as forças para conseguir o que deseja. Ama estar em contato com a natureza e valoriza quem o acompanha em suas aventuras.</p>
                </div>
            </div>
        </div>
    );
}

export default Card2;