import React from 'react';
import aries from './Aries.jpg';
import './styleCard1.css';

function Card1() {
    return (
        <div className='card1'>
            <img src={aries} className="App-logo" alt="logo" />
            <div className="info">
                <br></br>
                <h2>Mu</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>ÁRIES</h3>
                    <p>21/03 - 19/04</p>
                    <br></br>
                    <p>Áries é um signo de ação, velocidade e decisão.As pessoas do signo de Aries esbanjam alegria e contagiam quem está ao redor com sua ousadia e criatividade.</p>
                </div>
            </div>
        </div>

    );
}

export default Card1;