import React from 'react';
import capricornio from './Capricornio.jpg';
import './styleCard1.css';

function Card10() {
    return (
        <div className='card10'>
            <img src={capricornio} className="App-logo" alt="logo" />
            <div className='info'>
                <br></br>
                <h2>Shura</h2>
                
            </div>

            <div className='overlay'>
                <div className='text'>
                    <h3>CAPRICÓRNIO</h3>
                    <p>22/12 - 19/01</p>
                    
                    <p>As pessoas do signo de Capricórnio jamais se contentarão em apenas manter corpo e alma unidos. Eles sentem que precisam evoluir e realizar. Têm a necessidade de possuir alguma propriedade ou algum compromisso para cumprir, seja nos negócios, na política ou em áreas sociais e intelectuais. </p>
                </div>
            </div>
        </div>
    );
}

export default Card10;